import React, { Component } from 'react';
import './about.css';
import { Link, withRouter } from 'react-router-dom'
import Manifest from './component/Manifest.js'
import Contact from './component/Contact.js'
import LienUtile from './component/LienUtile.js';
import LesAmis from './component/LesAmis.js';
import scrollToComponent from 'react-scroll-to-component';


class About extends Component {



  componentDidMount() {
    window.scrollTo(0, 0)
  }

  render() {
    return (

      <div className="about" >
        {/* //////////////Logo //////////////// */}


        <div className="logoContent">
          <a className="texteLogo" onClick={() => { scrollToComponent(this.Manifest, { offset: -800, align: 'top', duration: 1500 }); }}  > A l'art U </a>
        </div>



        {/* ////////////// navbar //////////////// */}
        <div className="">
          <div className="navbarAbout">
            <div className="texteNav"  onClick={() => { scrollToComponent(this.Manifest, { offset: -160, align: 'top', duration: 1500 }); }}> Manifeste</div>
            <div className="texteNav">-</div>
            <div className="texteNav"  onClick={() => { scrollToComponent(this.Contact, { offset: -160, align: 'top', duration: 1500 }); }}> Contacts  </div>
            <div className="texteNav">-</div>
            <div className="texteNav"  onClick={() => { scrollToComponent(this.LienUtile, { offset: -160, align: 'top', duration: 1500 }); }}> Liens utils  </div>
            <div className="texteNav">-</div>
            <div className="texteNav"  onClick={() => { scrollToComponent(this.LesAmis, { offset: -160, align: 'top', duration: 1500 }); }}> Les amiEs de lart U </div>
            <Link style={{ textDecoration: 'none' }} to='/'>
              <div className="exit" > X </div>
            </Link >
          </div>
        </div>


    {/* ////////////// email //////////////// */}
        <div className="texteEmail" >
          <div>
            Pour tre tenu.e.s<br />
            inform.e.s lors du<br />
            lancement du projet ;<br />
            Facebook / Instagram,<br />
            Fais nous suivre ton<br />
            adresse e-mail :<br />
            <input type="text" placeholder="ton calisse de email" className="inputEmail" />
          </div>

        </div>


 {/* ////////////// manifeste //////////////// */}
 <Manifest ref={(section) => { this.Manifest = section; }}/>



 {/* ////////////// contact  //////////////// */}
  <Contact ref={(section) => { this.Contact = section; }}/>     
 {/* ////////////// lien Utile //////////////// */}

 <LienUtile ref={(section) => { this.LienUtile = section; }}/> 

           {/* ////////////// email //////////////// */}

 <LesAmis ref={(section) => { this.LesAmis = section; }}/> 

 </div>
    ); 


  }

}


export default withRouter(About)


